// ==========================================================================
// BEM Selectors
// ==========================================================================
@mixin e($element) {
    &__#{$element} {
        @content;
    }
}

@mixin m($modifier) {
    &--#{$modifier} {
        @content;
    }
}

// ==========================================================================
// Media Queries
// ==========================================================================
@mixin mq($break) {
    $lg: map-get($breaks, 'lg');
    @if $break=='lg' {
        @media (min-width: $lg) {
            @content;
        }
    }
}
// ==========================================================================
// Flexbox Configuration
// ==========================================================================
@mixin flex($direction: column, $wrap: null) {
    display: flex;
    flex-direction: $direction;
    flex-wrap: $wrap;
}